$_vikon(document).ready(() => {
    const autoHeight = $_vikon('#abitur_slider').data('autoheight');
    $_vikon('#abitur_slider .owl-img').ready(() => {
        iniOwlCarousel($_vikon('#abitur_slider'), autoHeight);
    });
});

function iniOwlCarousel(dom, enableAutoHeight) {
    const options = {
        navigation: true,
        slideSpeed: 300,
        paginationSpeed: 500,
        items: 1,
        singleItem: true,
        autoplay: true,
        autoplayTimeout: 8000,
        autoplaySpeed: 900,
        loop: true,
    };

    if (enableAutoHeight == true) {
        options.autoHeight = true;
    }
    dom.owlCarousel(options);
}
